.ums-datepicker {
	&__container {
		width: 100%;
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-end;
		position: relative;
		justify-content: space-between;

		ums-daterangepicker + ums-daterangepicker {
			margin-left: @m-small;
		}
	}

	&__wrapper {
		min-width: 320px;
		z-index: 20;
		text-align: right;
		display: inline-flex;
		flex-flow: row wrap;
		align-self: flex-end;
		justify-content: flex-end;
		position: relative;

		&:before {
			content: "Date";
			position: absolute;
			top: -20px;
			left: 0;
			display: block;
			width: 100%;
			.fz-sm();
			font-weight: @fw-bold;
			opacity: 0.35;
			text-align: left;
		}
	}

	&__button {
		@inputStyles();
		min-width: 100% !important;
		text-align: left;
		box-shadow: 2px 2px 2px rgba(0, 0, 0, 0);
		&:hover {
			box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
			cursor: pointer;
			.transition;
		}

		&__origin {
			line-height: 1.2 !important;
			position: relative;
			top: 1px;
		}

		&__compare {
			margin-top: 3px;
			font-size: 12px;
			opacity: 0.75;
		}
	}

	&__button.is-compared {
		padding-top: 8px;
		padding-bottom: 0;
		font-size: 13px !important;
		min-height: 46.66px;
		.transition();

		@media @min1600 {
			font-size: 13px !important;
		}
	}

	&__popup {
		position: absolute;
		left: auto;
		right: 0;
		display: flex;
		flex-flow: row nowrap;
		box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.15);
		z-index: 10;

		& {
			top: 80px;
			visibility: visible;
			opacity: 1;
			.transition;
		}

		&.is-hidden {
			top: 120px;
			visibility: hidden;
			opacity: 0;
			.transition;
		}

		&.is-align-left {
			right: auto;
			left: 0;
		}

		> *:nth-of-type(1) {
			border-radius: @br-base 0 0 @br-base;
		}

		&__select-compare {
			width: 320px;
			background: @color-base;
			padding: @m-base;
			color: @color-white;
			text-align: left;
			position: relative;

			&:before {
				content: "";
				position: absolute;
				top: -5px;
				left: 0;
				right: 0;
				margin: 0 auto;
				width: 15px;
				height: 15px;
				transform: rotate(45deg);
				border-radius: @br-base / 2;
				display: block;
				background: @color-base;
			}

			input[type="text"]:not(.numInput),
			select {
				@inputStylesDark();
				@inputSelectDark();
				width: 100% !important;

				&:focus {
					border-color: @color-white !important;
				}
			}

			input[type="checkbox"] {
				margin-top: -4px;
				margin-right: 5px;
			}
		}

		&__select-range {
			background: @color-white;
			position: relative;
			overflow: hidden;

			.flatpickr-calendar {
				position: relative !important;
				margin: @m-base / 1.5 @m-base;
				width: 760px;
				top: 0px !important;
				left: 0 !important;
				&:before {
					display: none;
				}
			}

			.ums-legenda__container {
				position: absolute !important;
				margin: 0 50px;
				width: auto;
				top: auto;
				bottom: @m-base / 1.5;
				left: auto;
				right: 0;
				z-index: 1;
				display: flex;
				flex-flow: row wrap;
				align-items: flex-end;
				justify-content: flex-end;
			}
			&.is-align-left {
				order: 1;
			}
		}

		&__button {
			margin-top: 10px;
		}

		&__close {
			z-index: 1;
			position: fixed;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: @color-overlay;
		}
	}
}

.ums-datepicker__container .ums-form__row + .ums-form__row {
	margin-top: 10px;
}

// OVERWRITES

.flatpickr-calendar {
	font-size: 11px !important;
	box-shadow: none !important;

	/* months */

	.flatpickr-current-month {
		font-size: 13px !important;
	}

	.flatpickr-months {
		padding: 5px 0;
		position: relative;
	}

	.flatpickr-prev-month,
	.flatpickr-next-month {
		top: 8px !important;
		padding: 4px 10px !important;
		&:hover svg {
			fill: @color-success !important;
		}
	}

	/* weeks */

	.flatpickr-weekdays {
		margin-top: 5px;
		height: 16px !important;
	}

	.flatpickr-weekdaycontainer {
		padding: 0 10px !important;
	}

	/* days */

	.flatpickr-day {
		border: none;

		&.startRange,
		&.endRange {
			background: @color-success !important;
		}

		&.today {
			background: @color-light-a !important;
		}

		&.inRange {
			background: fade(@color-graph01, 10%) !important;
			box-shadow: none !important;
		}

		&.selected {
			background: @color-graph01 !important;
		}
	}

	.dayContainer {
		padding: 0px 10px 5px 10px;
	}
}
