
.ums-reporting {
    @reporting-selector-width: 300px;

    &__selector {
        border-radius: 3px;
    }

    &__h2 {
        margin-bottom: 0;
        margin-top: 0;
    }

    &__top-bar {
        padding-top: 54px;
    }

    &__segments-container {
        display: flex;
        margin-top: 20px;
    }

    &__segment-select {
        width: @reporting-selector-width;
        position: relative; 
        border-top-right-radius: 0; 
        border-bottom-right-radius: 0;

        h3 {
            color: @color-white;
            font-size: 20px !important;
        }
    }

    &__segment-details {
        width: calc(100% - @reporting-selector-width);
        position: relative; 
        border-top-left-radius: 0; 
        border-bottom-left-radius: 0;

        hr {
            border-top: 1px solid @color-light-c;
            margin-left: -@m-base;
            margin-right: -@m-base;
        }

        &--desc {
            width: 360px;
            padding-right: 20px;
        }

        &--param {
            > div {
                background-color: #F6F4F4;
                padding: 20px;
                border-radius: 3px;
                display: flex;
                flex-wrap: wrap;
            }
        }
        
        &--stats {
            display: flex;
            font-size: 13px !important;
            
            > div {
                color: @color-black;
                font-weight: bold;
                margin-right: 25px;
                display: flex;
                align-items: center;

                > span {
                    margin-top: 2px;
                }

                > ums-icon {
                    color: @color-gray;
                    margin-right: 10px;
                }
            }
        }

        &--empty {
            h2 {
                text-align: center;
                color: @color-light-c;
                padding-top: 40px;
            }
            p {
                text-align: center;
                color: @color-black;
                opacity: 0.25;
                padding-bottom: 40px;
            }
        }
    }

    &__segment-bg-logo {
        position: absolute; 
        right: 20px; 
        bottom: -20px; 
        width: 100px;

        > svg > path:nth-child(1) {
            fill: rgba(255,255,255,0.1);
        }
        > svg > path:nth-child(2) {
            fill: @color-main;
        }
    }
    
    &__button-group {
        button {
            margin-right: 0.8em;
            padding: 10px 20px;
            border-radius: 3px;
            border: 1px solid @color-light-c;
            transition: all .2s ease-in-out;
            font-size: 13px;
            background-color: @color-light-a;

            &:last-child {
                margin-right: 0;
            }
        }

        .dissabled {
            cursor: not-allowed;
        }
        
        .selected {
            background-color: @color-white;
            color: @color-base;
            font-weight: 700;
            cursor: pointer;

            &:hover {
                background-color: @color-light-b;
            }
        }
    }

    &__graph-container {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
        justify-content: space-between;
        gap: 20px;

        h2 {
            width: 100%;
        }
    }

    &__graph {
        --padding: 20px;
        width: 100%;
        height: 280px;
        margin-bottom: 0px;
        padding: 0 var(--padding);

        hr {
            border-top: 1px solid @color-light-c;
            margin: 0 calc(var(--padding) * -1);
        }

        img {
            width: 100%;
        }
        
        .ums-fakegraph__loader {
            height: 145px;
        }

        .ums-fakegraph__line,
        .ums-fakegraph__number-holder {
            display: none;
        }

        &--placeholder {
            padding: 0;
        }
    }

    &__graph-canvas {
        margin-top: 0.5rem;
        height: 200px;
        width: 100%;

        .ums-chart,
        .ums-chart__wrapper {
            height: 100%;
        } 
    }
    
    &__graph-label {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.5rem 0;
    }

    &__graph-toggle {
        border: 1px solid #d1d4dc; 
        border-radius: 3px; 
        padding: 10px;
        font-weight: bolder;
        cursor: pointer;
        color: grey;
        display: flex;
        align-items: center;
        color: #D1D4DC;

        > #icon-hashtag, #icon-discount {
            height: 10px;
            width: 10px;
        }

        > label {
            pointer-events: none;
        }

        &--selected {
            color: @color-main;
        }
    }

    &__graph-toggle-switch {
        width: 20px;
        height: 4px;
        margin: 0 10px;
        border-radius: 3px;
        background-color: #D1D4DC;
        position: relative;

        > input {
            height: 0;
        }

        > span {
            background-color: @color-main;
            height: 10px;
            width: 10px;
            border-radius: 50%;
            position: absolute;
            transition: .4s;
            top: -3px;
            left: 0;
        }

        &--right {
            transform: translateX(10px);
        }
    }

    // tooltip container
    &__info-text {
        bottom: auto;
        top: 60px;
        height: auto;
        margin: 5px;
        z-index: 10;
        
        .fz-md();

        // point at the top
        &::after {
            left: 50%;
            top: 0;
        }
    }
}