// CHART

.ums-chart {
  &__container {
    border: 1px solid red;
  }

  &__header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    flex: 1 1 0;
    margin: 0;
  }

  &__right-align {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;

    > * + * {
      margin-left: 20px;
    }
  }
}

.ums-chart-persona {
  &__chart {
    margin-left: 41px;
    z-index: 1;
    &::before {
      content: "";
      position: absolute;
      bottom: 33px;
      left: 10px;
      right: 12px;
      top: auto;
      border-bottom: 1px solid #d9d9d9;
      z-index: -1;
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    margin-left: 0;
    pointer-events: none;
    width: 100%;
  }
  &__image-holder {
    width: 31px;
    height: 31px;
    border-radius: 2px;
    border: 2px solid @color-border;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__item {
    display: flex;
    height: 50px;
    position: relative;
    // border-bottom: 1px solid red;
  }
  &__icon-holder {
    display: flex;
  }
  &__line {
    width: 2px;
    height: 31px;
    background: #fff;
    margin-right: 2px;
  }
  &__icon {
    width: 21px;
    height: 21px;
    margin: auto;
    top: 0;
    position: relative;
    border-radius: 2px;
  }
  &__name {
    margin-left: 15px;
    line-height: 1;
    font-weight: 600;
    color: fade(@color-gray, 60%);
    height: 12px;
    z-index: 1;
    background: #fff;
    padding-right: 6px;
    padding-top: 1px;
    &.s-active {
      color: #000;
    }
  }
  &__empty-bar {
    width: calc(100% - 63px);
    background: #f6f4f4;
    height: 10px;
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    left: 51px;
    z-index: 0;
  }
  &__description {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &__legend-list {
    display: flex;
    flex-wrap: wrap;
    font-size: 11px;
    padding: 2px 0px 0px 10px;
    font-weight: 600;
    margin-left: 41px;
    margin-bottom: 30px;
    list-style: none;
    color: fade(@color-gray, 60%);

    & > * {
      border-left: 1px dashed @color-base;
      padding-left: 8px;
      margin-top: 3px;
      margin-bottom: 3px;
      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }
  &__tooltip {
    position: absolute;
    opacity: 0;
    pointer-events: none;
    padding: 5px 8px;
    transition: opacity 0.15s ease;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    z-index: 10;
    border-radius: 8px;
    font-weight: bold;
    font-size: 13px;
    min-width: 52px;
    font-family: Lato, Helvetica Neue, Helvetica, Arial, sans-serif;
    & > table {
      font-weight: 400;
      text-align: left;
      background: transparent;
    }
    &::before {
      content: "";
      width: 0;
      height: 0;
      border-bottom: 6px solid rgba(0, 0, 0, 0);
      border-left: 6px solid rgba(0, 0, 0, 0.8);
      position: absolute;
      top: 0;
      left: auto;
      right: 8px;
      transform-origin: top left;
      transform: rotate(45deg) translateY(-100%);
    }
  }
  &__tooltip-color {
    width: 11px;
    height: 11px;
    display: inline-block;
    border-width: 2px;
    margin-right: 3px;
    top: 2px;
    position: relative;
  }
}

.ums-chart-profile-donut {
  &__chart-wrapper {
    position: relative;
    padding: 25px 0;
    display: flex;
    align-items: center;
    min-height: 260px;
  }
  &__chart-holder {
    position: absolute;
    flex: 0 0 auto;
    width: 100%;
  }
  &__chart-item {
    left: -18%;
    width: 90%;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    width: 54%;

    & > strong {
      padding-top: 15px;
      font-size: 30px;
      font-weight: 900;
      max-width: 65%;
      overflow: hidden;
      line-height: 1;
      text-overflow: ellipsis;
    }
  }
  &__legend {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 50%;
    margin-left: 60%;
    & > * + * {
      margin-top: 15px;
    }
  }
  &__legend-item {
    display: flex;
    flex-direction: column;
    color: @color-main;
    list-style: none;
    position: relative;
  }
  &__legend-dot {
    position: absolute;
    top: 3px;
    left: 0;
    transform: translateX(calc((100% + 10px) * -1));
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background: currentColor;
  }
  &__legend-name {
    flex: 1;
  }
  &__legend-value {
    color: #000;
    & small {
      opacity: 0.5;
      margin-left: 6px;
    }
  }
}

.ums-chart-donut {
  &__chart-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 167px;
  }
  &__chart-holder {
    position: absolute;
    flex: 0 0 auto;
    width: 100%;
  }
  &__chart-item {
    left: -18%;
    width: 90%;
  }
  &__overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    width: 54%;

    & > strong {
      padding-top: 15px;
      font-size: 30px;
      font-weight: 900;
      max-width: 65%;
      overflow: hidden;
      line-height: 1;
      text-overflow: ellipsis;
    }
  }
  &__sidebar {
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 30%;
    margin-left: 70%;
    & > * + * {
      margin-top: 5px;
    }
  }
  &__sidebar-item {
    display: flex;
    flex-direction: column;
    color: @color-main;
    list-style: none;
    position: relative;
  }
  &__sidebar-dot {
    position: absolute;
    top: 6px;
    left: 0;
    transform: translateX(calc((100% + 10px) * -1));
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background: currentColor;
  }
  &__sidebar-name {
    flex: 1;
  }
  &__sidebar-value {
    display: flex;
    color: #000;
    & small {
      white-space: nowrap;
      opacity: 0.5;
      margin-left: 6px;
    }
  }
  &__legend {
    display: flex;
    column-gap: 27px;
    margin-top: 7px;
  }
  &__legend-item {
    position: relative;
  }
}

.ums-chart-profile-growth {
  &__chart-wrapper {
    position: relative;
    padding: 15px 0 25px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 260px;
  }

  &__chart-holder {
    width: 100%;
    padding: 0 20px;
  }

  &__legend {
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 15px 0 0;

    & > * + * {
      margin-left: 40px !important;
    }
  }

  &__legend-item {
    display: flex;
    flex-direction: column;
    color: @color-main;
    list-style: none;
    position: relative;
    margin-left: 20px;
  }
}

.ums-chart-profile-potential {
  &__chart-wrapper {
    padding: 20px;
  }
  &__chart-holder {
    padding-top: 100%;
    height: 0;
    position: relative;
  }
  &__chart-item {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before,
    &::after {
      content: "";
      width: 3px;
      height: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      background: linear-gradient(
        -180deg,
        hsl(150, 65%, 46%),
        hsl(125, 65%, 46%),
        hsl(100, 65%, 46%),
        hsl(75, 65%, 46%),
        hsl(50, 65%, 46%),
        hsl(25, 65%, 46%),
        hsl(0, 65%, 46%)
      );
      z-index: 1;
    }
    &::after {
      transform: rotate(90deg);
    }
  }
  &__ring-holder {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
  }
  &__ring {
    border-radius: 50%;
    border: 12px solid @color-gray;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.05;
    margin: auto;
    &:nth-child(2) {
      width: 62%;
      height: 62%;
      opacity: 0.14;
    }
    &:nth-child(3) {
      width: 25%;
      height: 25%;
      opacity: 0.3;
    }
  }
  &__names {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    list-style: none;
    bottom: 0;
    right: 0;
    & > * {
      position: absolute;
      width: 100%;
      text-align: center;
      justify-content: center;
      transform-origin: top left;
      &:nth-child(1) {
        left: 50%;
        transform: translate(-50%, -100%);
      }
      &:nth-child(2) {
        right: 0;
        left: auto;
        top: 50%;
        transform-origin: top right;
        transform: rotate(90deg) translate(50%, -100%);
      }
      &:nth-child(3) {
        left: 50%;
        top: auto;
        bottom: 0;
        transform: translate(-50%, 100%);
      }
      &:nth-child(4) {
        left: 0;
        top: 50%;
        transform: rotate(-90deg) translate(-50%, -100%);
      }
    }
  }
  &__holds-pin {
    width: 50%;
    height: 50%;
    transform: translate(50%, -50%);
    position: relative;
  }
  &__pin {
    --color: 152;
    width: 15px;
    height: 15px;
    color: hsl(var(--color), 65%, 46%);
    position: absolute;
    z-index: 2;
    top: auto;
    bottom: 0%;
    left: 0%;
    right: 0;
    opacity: 0;
    transform: scale(0);
    transition: transform 0.8s cubic-bezier(0.06, 0.44, 0, 1.2);
    &.s-visible {
      transform: scale(1);
      opacity: 1;
    }

    & > span {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      color: inherit;
      background: currentColor;
      z-index: 1;
    }
    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      border-radius: 50%;
      transform: scale(2.5);
      color: inherit;
      background: currentColor;
      z-index: 0;
      filter: brightness(1.3) grayscale(0.8);
      opacity: 0.3;
      pointer-events: none;
    }
    &::after {
      filter: brightness(1.2) grayscale(0.4);
      animation: animateDot 2s ease infinite;
    }
  }
}

@keyframes animateDot {
  0% {
    transform: scale(1);
    opacity: 0.8;
  }
  100% {
    transform: scale(2.5);
    opacity: 0;
  }
}
